/******************* HTML elements ************************/
@media only screen and (max-width: 600px) and (orientation: portrait) {
  .btn {
    font-weight: bold;
  }
  .middle-container {
    margin: 0;
    width: 100%;
  }

  .skill-row {
    width: 100%;
    margin: 0;
    text-align: center;
  }
  .skill-rowWings {
    width: 100%;
    margin: 0;
    text-align: center;
  }

  h1 {
    font-size: 5rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.5rem;
  }
  p {
    font-size: 1rem;
  }
  .mountain-img {
    /* height: 300px; */
    width: 75%;
  }

  .middle-image {
    height: 100px;
    margin-bottom: 5px;
  }
  .bottom-image {
    height: 100px;
    margin-bottom: 5px;
  }
  .third-image {
    height: 100px;

    margin-right: 40px;
    margin-bottom: 5px;
  }
}

body {
  margin: 0;
  text-align: center;
  font-family: "Merriweather", serif;
  color: #40514e;
  /*
